import "./styles.css";
import React from "react";
import { motion } from "framer-motion/dist/framer-motion";
import { ScenesContext } from "./scenesContext";

const variantsMain = {
  initial: {
    opacity: 0
  },
  animate: {
    opacity: 1,
    transition: { duration: 0.4, ease: "easeOut" }
  },
  exit: {
    opacity: 0,
    transition: { duration: 0.4, ease: "easeIn" }
  }
};

export default function Content() {
  const { currIndex, scenes } = React.useContext(ScenesContext);

  return (
    <motion.div
      variants={variantsMain}
      initial="initial"
      animate="animate"
      exit="exit"
      className="content"
    >
      <div className="content-text">{scenes[currIndex].text}</div>

      <img
        className="content-image"
        src={scenes[currIndex].image}
        alt="Aisha"
      />
    </motion.div>
  );
}
