import "./styles.css";
import React from "react";
import { useNavigate } from "react-router-dom";
import { motion } from "framer-motion/dist/framer-motion";
import { ScenesContext } from "./scenesContext";

const variantsButtons = {
  initial: {
    x: -100,
    opacity: 0
  },
  animate: {
    x: 24,
    opacity: 1,
    transition: { duration: 0.4, ease: "easeOut" }
  },
  exit: {
    opacity: 0,
    y: window.innerHeight,
    transition: { duration: 0.6, ease: "easeIn" }
  }
};

export default function Buttons() {
  const { currIndex, scenes, updateScene } = React.useContext(ScenesContext);

  let navigate = useNavigate();

  function handleClick(next) {
    updateScene(next);
    navigate(`/${next}`);
  }

  return (
    <motion.div className="options">
      <motion.div
        variants={variantsButtons}
        initial="initial"
        animate="animate"
        exit="exit"
        className="buttons"
      >
        {scenes &&
          scenes[currIndex].buttons.map((button) => {
            return (
              <div
                key={button.text}
                className="button"
                onClick={() => handleClick(button.next)}
              >
                <div className="button-number"> {button.number}</div>

                <span> {button.text}</span>
              </div>
            );
          })}
      </motion.div>
    </motion.div>
  );
}
